<template>
      <div>
            <div ref="point" :class="classes" :style="styles">
                  <slot></slot>
            </div>
            <div v-show="slot" :style="slotStyle"></div>
      </div>
</template>

<script>
import { on, off } from '../../utils/dom';
const prefixCls = 'ivue-affix';

export default {
      name: prefixCls,
      props: {
            /*
            * 
            * 
            * @type {Number}
            */
            offsetTop: {
                  type: Number,
                  default: 0
            },
            /*
            * 
            * 
            * @type {Number}
            */
            offsetBottom: {
                  type: Number,
                  default: -1
            }
      },
      data () {
            return {
                  /*
                  * 
                  * 
                  * @type {Boolean}
                  */
                  affix: false,
                  /*
                  * slot 
                  * 
                  * @type {Boolean}
                  */
                  slot: false,
                  /*
                  * styles
                  * 
                  * @type {Object}
                  */
                  styles: {},
                  /*
                  * slotStyle
                  * 
                  * @type {Object}
                  */
                  slotStyle: {}
            }
      },
      computed: {
            // 
            offsetType () {
                  let type = 'top';

                  // 
                  if (this.offsetBottom >= 0) {
                        type = 'bottom';
                  }

                  return type;
            },
            // class fixed
            classes () {
                  return [
                        {
                              [`${prefixCls}`]: this.affix
                        }
                  ]
            }
      },
      mounted () {
            // 
            on(window, 'scroll', this.handleScroll);
            on(window, 'resize', this.handleScroll);
      },
      // 
      beforeDestroy () {
            // 
            off(window, 'scroll', this.handleScroll);
            off(window, 'resize', this.handleScroll);
      },
      methods: {
            // 
            getScroll (target, top) {
                  const prop = top ? 'pageYOffset' : 'pageXOffset';
                  const method = top ? 'scrollTop' : 'scrollLeft';

                  //  window  pageYOffset || pageXOffset
                  let ret = target[prop];

                  //   pageYOffset || pageXOffset 
                  if (typeof ret !== 'number') {
                        //  scrollTop || scrollLeft
                        ret = window.document.documentElement[method];
                  }

                  return ret;
            },
            // 
            getOffset (element) {
                  // 
                  const rect = element.getBoundingClientRect();

                  // 
                  const scrollTop = this.getScroll(window, true);
                  // 
                  const scrollLeft = this.getScroll(window);

                  const docEl = window.document.body;
                  // 
                  const clientTop = docEl.clientTop || 0;
                  //
                  const clientLeft = docEl.clientTop || 0;

                  return {
                        //  +  - ()
                        top: rect.top + scrollTop - clientTop,
                        //  +  - ()
                        left: rect.left + scrollLeft - clientLeft
                  }

            },
            // 
            handleScroll () {
                  // 
                  const affix = this.affix;

                  // 
                  const scrollTop = this.getScroll(window, true);
                  // 
                  const elOffset = this.getOffset(this.$el);
                  // window
                  const windowHeight = window.innerHeight;
                  // 
                  const elHeight = this.$el.getElementsByTagName('div')[0].offsetHeight;

                  //  Top
                  //      < windo   
                  if ((elOffset.top - this.offsetTop) < scrollTop && this.offsetType === 'top' && !affix) {
                        // 
                        this.affix = true;
                        // slot
                        this.slotStyle = {
                              width: this.$refs.point.clientWidth + 'px',
                              height: this.$refs.point.clientHeight + 'px'
                        }
                        this.slot = true;

                        // 
                        this.styles = {
                              top: `${this.offsetTop}px`,
                              left: `${elOffset.left}px`,
                              width: `${this.$el.offsetWidth}px`
                        }

                        // 
                        this.$emit('on-change', true);
                  }
                  else if ((elOffset.top - this.offsetTop) > scrollTop && this.offsetType == 'top' && affix) {
                        this.slot = false;
                        this.slotStyle = {};
                        this.affix = false;
                        this.styles = null;

                        // 
                        this.$emit('on-change', false);
                  }

                  //  Bottom
                  if ((elOffset.top + this.offsetBottom + elHeight) > (scrollTop + windowHeight) && this.offsetType == 'bottom' && !affix) {
                        this.affix = true;
                        this.styles = {
                              bottom: `${this.offsetBottom}px`,
                              left: `${elOffset.left}px`,
                              width: `${elOffset.top}px`
                        }

                        //  // 
                        this.$emit('on-change', true);
                  }
                  else if ((elOffset.top + this.offsetBottom + elHeight) < (scrollTop + windowHeight) && this.offsetType == 'bottom' && affix) {
                        this.affix = false;
                        this.styles = null;

                        //  // 
                        this.$emit('on-change', false);
                  }

            }
      }
}
</script>



