export default {
      props: {
            /*
            *  tab  name v-model 
            * 
            * @type{String,Number}
            */
            value: [String, Number],
            /*
            * 
            * 
            * @type{String,Number}
            */
            height: {
                  type: [String, Number],
                  default: undefined,
                  validator: v => !isNaN(parseInt(v))
            },
            /*
            * 
            * 
            * @type{Boolean}
            */
            centered: Boolean,
            /*
            * 
            * 
            * @type{Boolean}
            */
            right: Boolean,
            /*
            * 
            * 
            * @type{String}
            */
            sliderColor: {
                  type: String,
                  default: 'primary'
            },
            /*
            * 
            * 
            * @type{Boolean}
            */
            hideSlider: {
                  type: Boolean,
                  default: false
            },
            /*
            * 
            * 
            * @type{Boolean}
            */
            showArrows: Boolean,
            nextIcon: {
                  type: String,
                  default: 'chevron_right'
            },
            prevIcon: {
                  type: String,
                  default: 'chevron_left'
            },
            /*
            * 
            * 
            * @type{Number}
            */
            arrowsMargin: {
                  type: Number,
                  default: 40
            },
            /*
            * 
            * 
            * @type {Boolean}
            */
            fixedTabs: Boolean
      }
}


