/**
 *  ->  viewPort 
 * cell()
 * 
 * 0
 */

export default class Section {
      constructor({ x, y, width, height }) {
            this.height = height;
            this.width = width;
            this.x = x;
            this.y = y;

            // 
            this._indexMap = {};

            // 
            this._cellIndex = [];
      }


      // 
      addCellIndex ({ index }) {
            if (!this._indexMap[index]) {
                  // 
                  this._indexMap[index] = true;
                  // 
                  this._cellIndex.push(index)
            }
      }

      // 
      getCellIndices () {
            return this._cellIndex;
      }


      /**  */
      toString () {
            return `${this.x},${this.y} ${this.width}x${this.height}`
      }
}


