<template>
      <div
            :class="classes"
            v-touch="{
                                          left: e => (e.offsetX < -15) && arrowEvent(1),
                                          right: e => (e.offsetX > 15) && arrowEvent(-1)
                                    }"
      >
            <!--  -->
            <IvueButton
                  :class="arrowClasses"
                  class="left ivue-icon-button"
                  @click="arrowEvent(-1)"
                  flat
                  icon
            >
                  <IvueIcon>{{leftArrow}}</IvueIcon>
            </IvueButton>
            <!--  -->
            <div :class="[prefixCls + '-list']">
                  <div
                        :class="[prefixCls + '-track', showCopyTrack ? '' : 'higher']"
                        :style="trackStyles"
                        ref="originTrack"
                  >
                        <slot></slot>
                  </div>
                  <!-- loop -->
                  <div
                        :class="[prefixCls + '-track', showCopyTrack ? 'higher' : '']"
                        :style="copyTrackStyles"
                        ref="copyTrack"
                        v-if="loop"
                  ></div>
            </div>
            <!--  -->
            <IvueButton
                  :class="arrowClasses"
                  class="right ivue-icon-button"
                  @click="arrowEvent(1)"
                  flat
                  icon
            >
                  <IvueIcon>{{rightArrow}}</IvueIcon>
            </IvueButton>
            <!--  -->
            <ul :class="dotsClasses">
                  <li
                        v-for="index in slides.length"
                        :key="index"
                        :class="[index -1 === currentIndex ? prefixCls + '-active' : '']"
                        @click="dotsEvent('click', index-1)"
                        @mouseover="dotsEvent('hover', index-1)"
                  >
                        <button type="button" :class="[radiusDot ? 'radius' : '']"></button>
                  </li>
            </ul>
      </div>
</template>

<script>
import IvueIcon from '../ivue-icon/ivue-icon';
import IvueButton from '../ivue-button/ivue-button';

import Touch from '../../utils/directives/touch';
import { on, off } from '../../utils/dom';
import { getStyle, oneOf } from '../../utils/assist';

const prefixCls = 'ivue-carousel';

export default {
      name: prefixCls,
      directives: { Touch },
      props: {
            /*
            * 
            * 
            * @type {String}
            * 
            * hoveralwaysnever
            */
            arrow: {
                  type: String,
                  default: 'hover',
                  validator (value) {
                        return oneOf(value, ['hover', 'always', 'never'])
                  }
            },
            /*
            * 
            * 
            * @type {String}
            */
            leftArrow: {
                  type: String,
                  default: 'keyboard_arrow_left'
            },
            /*
            * 
            * 
            * @type {String}
            */
            rightArrow: {
                  type: String,
                  default: 'keyboard_arrow_right'
            },
            /*
            * 
            * 
            * @type {String}
            */
            easing: {
                  type: String,
                  default: 'ease'
            },
            /*
            * 
            * 
            * @type {Boolean}
            */
            loop: {
                  type: Boolean,
                  default: false
            },
            /*
            * 
            * 
            * @type {String | Number}
            * 
            *  auto  px
            */
            height: {
                  type: [String, Number],
                  default: 'auto',
                  validator (value) {
                        return value === 'auto' || Object.prototype.toString.call(value) === '[object Number]';
                  }
            },
            /*
            * 
            * 
            * @type {Number}
            */
            value: {
                  type: Number,
                  default: 0
            },
            /*
            * 
            * 
            * @type {Boolean}
            */
            autoplay: {
                  type: Boolean,
                  default: false
            },
            /*
            * 
            * 
            * @type {Number}
            */
            autoplaySpeed: {
                  type: Number,
                  default: 2000
            },
            /*
            *  inside outsidenone	
            * 
            * @type {String}
            */
            dots: {
                  type: String,
                  default: 'inside',
                  validator (value) {
                        return oneOf(value, ['inside', 'outside', 'none'])
                  }
            },
            /*
            * 
            * 
            * @type {Boolean}
            */
            radiusDot: {
                  type: Boolean,
                  default: false
            },
            /*
            *  clickhover
            * 
            * @type {String}
            */
            trigger: {
                  type: String,
                  default: 'click',
                  validator (value) {
                        return oneOf(value, ['click', 'hover'])
                  }
            }
      },
      data () {
            return {
                  prefixCls: prefixCls,
                  /*
                  * 
                  * @type {Number}
                  */
                  listWidth: 0,
                  /*
                  * 
                  * @type {Number}
                  */
                  trackWidth: 0,
                  /*
                  * 
                  * @type {Number}
                  */
                  trackOffset: 0,
                  /*
                  * 
                  * @type {Number}
                  */
                  trackIndex: this.value,
                  /*
                  * 
                  * @type {Number}
                  */
                  trackCopyOffset: 0,
                  /*
                  * 
                  * @type {Number}
                  */
                  trackCopyIndex: this.value,
                  /*
                  * 
                  * @type {Boolean}
                  */
                  showCopyTrack: false,
                  /*
                  * 
                  * @type {Array}
                  */
                  slides: [],
                  /*
                  * 
                  * @type {Array}
                  */
                  slideInstances: [],
                  /*
                  * 
                  * @type {Number}
                  */
                  currentIndex: this.value,
                  /*
                  * 
                  * @type {Number}
                  */
                  hideTrackPos: -1
            }
      },
      mounted () {
            // 
            this.handleResize();
            // 
            this.setAutoplay();

            // 
            on(window, 'resize', this.handleResize);
      },
      beforeDestroy () {
            // 
            off(window, 'resize', this.handleResize);
      },
      computed: {
            // 
            classes () {
                  return [
                        `${prefixCls}`
                  ]
            },
            // 
            arrowClasses () {
                  return [
                        `${prefixCls}-arrow`,
                        `${prefixCls}-arrow-${this.arrow}`
                  ]
            },
            // 
            trackStyles () {
                  return {
                        width: `${this.trackWidth}px`,
                        transform: `translate3d(${-this.trackOffset}px, 0px, 0px)`,
                        transition: `transform 500ms ${this.easing}`
                  }
            },
            // 
            copyTrackStyles () {
                  return {
                        width: `${this.trackWidth}px`,
                        transform: `translate3d(${-this.trackCopyOffset}px, 0px, 0px)`,
                        transition: `transform 500ms ${this.easing}`,
                        position: 'absolute',
                        top: 0
                  }
            },
            // dots 
            dotsClasses () {
                  return [
                        `${prefixCls}-dots`,
                        `${prefixCls}-dots-${this.dots}`
                  ]
            }
      },
      methods: {
            _initData () {
                  this.currentIndex = this.value;
                  this.trackIndex = this.value;
            },
            //   loop 
            initCopyTrackDom () {
                  this.$nextTick(() => {
                        this.$refs.copyTrack.innerHTML = this.$refs.originTrack.innerHTML;
                  });
            },
            // slot
            slotChange (thisChild) {
                  this.$nextTick(() => {
                        this.slides = [];
                        this.slideInstances = [];

                        // 
                        this.updateSlides(true);
                        // 
                        this.updatePos();
                        // 
                        this.updateOffset();
                  })
            },
            // 
            findChild (cb) {
                  const find = function (child) {
                        const name = child.$options.componentName;

                        if (name) {
                              cb(child);
                        }
                        else if (child.$children.length) {
                              child.$children.forEach((innerChild) => {
                                    find(innerChild, cb);
                              });
                        }
                  };

                  if (this.slideInstances.length || !this.$children) {
                        this.slideInstances.forEach((child) => {
                              find(child);
                        });
                  } else {
                        this.$children.forEach((child) => {
                              find(child);
                        });
                  }
            },
            // 
            updateSlides (init) {
                  let slides = [];
                  let index = 1;

                  this.findChild((child) => {
                        slides.push({
                              $el: child.$el
                        });
                        child.index = index++;

                        if (init) {
                              this.slideInstances.push(child);
                        }
                  });

                  // 
                  this.slides = slides;

                  // 
                  this.updatePos();
            },
            // 
            updatePos () {
                  this.findChild((child) => {
                        // 
                        child.width = this.listWidth;
                        child.height = typeof this.height === 'number' ? `${this.height}px` : this.height;
                  });

                  // 
                  this.trackWidth = (this.slides.length || 0) * this.listWidth;
            },
            // 
            updateOffset () {
                  this.$nextTick(() => {
                        let offset = this.trackCopyIndex > 0 ? -1 : 1;
                        // 
                        this.trackOffset = this.trackIndex * this.listWidth;
                        // 
                        this.trackCopyOffset = this.trackCopyIndex * this.listWidth + offset;
                  });
            },
            // 
            updateTrackPos (index) {
                  // 
                  if (this.showCopyTrack) {
                        // 
                        this.trackIndex = index
                  }
                  else {
                        // 
                        this.trackCopyIndex = index;
                  }
            },
            // 
            updateTrackIndex (index) {
                  // 
                  if (this.showCopyTrack) {
                        // 
                        this.trackCopyIndex = index;
                  }
                  else {
                        // 
                        this.trackIndex = index;
                  }

                  // 
                  this.currentIndex = index;
            },
            // 
            handleResize () {
                  // 
                  this.listWidth = parseInt(getStyle(this.$el, 'width'));
                  // 
                  this.updatePos();
                  // 
                  this.updateOffset();
            },
            add (offset) {
                  // 
                  let slidesLen = this.slides.length;

                  // 
                  if (this.loop) {
                        if (offset > 0) {
                              // 
                              this.hideTrackPos = -1;
                        }
                        else {
                              // 
                              this.hideTrackPos = slidesLen;
                        }

                        // 
                        this.updateTrackPos(this.hideTrackPos);
                  }

                  //     
                  const oldIndex = this.showCopyTrack ? this.trackCopyIndex : this.trackIndex;

                  let index = oldIndex + offset;

                  while (index < 0) {
                        index += slidesLen;
                  }

                  // 
                  if (((offset > 0 && index === slidesLen) || (offset < 0 && index === slidesLen - 1)) && this.loop) {
                        //   - 1

                        // 
                        this.showCopyTrack = !this.showCopyTrack;
                        // 
                        this.trackIndex += offset;
                        //  
                        this.trackCopyIndex += offset;
                  }
                  else {
                        if (!this.loop) {
                              index = index % this.slides.length;
                        }

                        // 
                        this.updateTrackIndex(index);
                  }

                  // 
                  this.currentIndex = index === this.slides.length ? 0 : index;

                  // 
                  this.$emit('on-change', oldIndex, this.currentIndex);
                  this.$emit('current-index', this.currentIndex);
            },
            // 
            setAutoplay () {
                  window.clearInterval(this.timer);

                  if (this.autoplay) {
                        this.timer = window.setInterval(() => {
                              this.add(1);
                        }, this.autoplaySpeed);
                  }
            },
            // 
            arrowEvent (offset) {
                  this.setAutoplay();
                  this.add(offset);
            },
            // dots 
            dotsEvent (event, index) {
                  //     
                  let currentIndex = this.showCopyTrack ? this.trackCopyIndex : this.trackIndex;

                  if (event === this.trigger && currentIndex !== index) {
                        // 
                        this.updateTrackIndex(index);

                        // 
                        this.$emit('current-index', index);

                        // 
                        this.setAutoplay();
                  }
            }
      },
      watch: {
            // 
            autoplay () {
                  this.setAutoplay();
            },
            // 
            autoplaySpeed () {
                  this.setAutoplay();
            },
            // 
            trackIndex () {
                  this.updateOffset();
            },
            // =
            trackCopyIndex () {
                  this.updateOffset();
            },
            // 
            height () {
                  this.updatePos();
            },
            // 
            value (value) {
                  this.updateTrackIndex(value);
                  this.setAutoplay();
            },
            loop (value) {
                  // 
                  this.handleResize();
                  // 
                  this.setAutoplay();
                  // 
                  this._initData();
                  // 
                  this.updateOffset();

                  if (value) {
                        this.initCopyTrackDom();
                  }
                  else {
                        this.showCopyTrack = false;
                  }


            }
      },
      components: {
            IvueIcon,
            IvueButton
      }
}
</script>




