<template>
      <div :class="prefixCls" :style="outerStyle" ref="outer" @scroll.passive="onScroll">
            <div :class="`${prefixCls}--container`" :style="containerStyle">
                  <div
                        class="cell-container"
                        v-for="item in displayItems"
                        :key="item.key"
                        :style="getComputedStyle(item)"
                  >
                        <slot name="cell" :data="item.data"></slot>
                  </div>
            </div>
      </div>
</template>

<script>
import GroupMessage from './group-message';

const prefixCls = 'ivue-virtual-collection';

// 
const sensitivity = 0;

export default {
      name: prefixCls,
      props: {
            /**
             * 
             * 
             * @type {Array}
             */
            collection: {
                  type: Array,
                  required: true
            },
            /**
             * 
             * 
             * @type {Function}
             */
            cellSizeAndPositionGetter: {
                  type: Function,
                  required: true
            },
            /**
             * 
             * 
             * @type {Number}
             */
            height: {
                  type: Number,
                  required: true,
                  validator (value) {
                        return value >= 0;
                  }
            },
            /**
             * 
             * 
             * @type {Number}
             */
            width: {
                  type: Number,
                  required: true,
                  validator (value) {
                        return value >= 0;
                  }
            },
            /**
             *Collection
             * 
             * 
             * @type {Number}
             */
            sectionSize: {
                  type: Number,
                  default: 300
            },
            /**
             * 
             * 
             * @type {Number}
             */
            bottomSensitivity: {
                  type: Number,
                  default: sensitivity
            }
      },
      data () {
            return {
                  prefixCls: prefixCls,
                  // 
                  totalHeight: 0,
                  // 
                  totalWidth: 0,
                  // 
                  displayItems: [],
                  // 
                  groupMessage: [],
                  onBottom: false,
                  onTop: false
            }
      },
      created () {
            this.onCollectionChanged()
      },
      computed: {
            // 
            outerStyle () {
                  return {
                        width: `${this.width}px`,
                        height: `${this.height}px`
                  };
            },
            // 
            containerStyle () {
                  return {
                        width: `${this.totalWidth}px`,
                        height: `${this.totalHeight}px`
                  };
            }
      },
      methods: {
            // 
            onCollectionChanged () {
                  let collection = this.collection;

                  // 
                  if (collection.length > 0 && collection[0].group === undefined) {
                        collection = [{ group: collection }];
                  }

                  // 
                  collection.forEach((groupContainer, index) => {
                        // 
                        const groupIndex = index;

                        // 
                        const unwatch = this.$watch(
                              () => groupContainer,
                              () => this.onGroupChanged(groupContainer.group, groupIndex),
                              // 
                              { deep: true }
                        )

                        //  
                        this.groupMessage.push(new GroupMessage(
                              groupContainer.group,
                              groupIndex,
                              this.sectionSize,
                              this.cellSizeAndPositionGetter,
                              unwatch
                        ));

                  });

                  // 
                  this.updateGridSize();
                  // 
                  this.flushDisplayItems();
            },
            // 
            onGroupChanged (group, index) {
                  // 
                  this.groupMessage[index].updateGroup(group);

                  // 
                  this.updateGridSize();
                  // 
                  this.flushDisplayItems();
            },
            // 
            flushDisplayItems () {
                  let scrollTop = 0;
                  let scrollLeft = 0;

                  // 
                  if (this.$refs.outer) {
                        scrollTop = this.$refs.outer.scrollTop;
                        scrollLeft = this.$refs.outer.scrollLeft;
                  }

                  const displayItems = [];

                  this.groupMessage.forEach((groupMessage, groupIndex) => {

                        // 
                        let indices = groupMessage.getCellIndices({
                              height: this.height,
                              width: this.width,
                              x: scrollLeft,
                              y: scrollTop
                        });

                        indices.forEach(itemIndex => {
                              // 
                              displayItems.push(Object.freeze({
                                    groupIndex,
                                    itemIndex,
                                    key: displayItems.length,
                                    // 
                                    ...groupMessage.getItem(itemIndex)
                              }));
                        });
                  });

                  if (window.requestAnimationFrame) {
                        window.requestAnimationFrame(() => {
                              this.displayItems = displayItems;
                              this.$forceUpdate();
                        })
                  } else {
                        this.displayItems = displayItems;
                        this.$forceUpdate();
                  }
            },
            // 
            updateGridSize () {
                  this.totalHeight = Math.max(...this.groupMessage.map(item => item.totalHeight));
                  this.totalWidth = Math.max(...this.groupMessage.map(item => item.totalWidth));
            },
            //  
            getComputedStyle (displayItems) {
                  if (!displayItems) {
                        return;
                  }

                  //
                  //
                  const groupMessage = this.groupMessage[displayItems.groupIndex];
                  if (!groupMessage) {
                        return;
                  }

                  // 
                  const cellMetadata = groupMessage.getCellMetadata(displayItems.itemIndex);
                  if (!cellMetadata) {
                        return;
                  }

                  const { x, y, width, height } = cellMetadata;

                  return {
                        transform: `translate3d(${x}px,${y}px,0)`,
                        // left: `${x}px`,
                        // top: `${y}px`,
                        width: `${width}px`,
                        height: `${height}px`
                  }

            },
            // 
            onScroll (el) {
                  let target = el.target;

                  // 
                  let displacement = target.scrollHeight - target.clientHeight - target.scrollTop;

                  // 
                  if (displacement <= this.bottomSensitivity) {
                        this.onBottom = true;
                  }
                  // 
                  else if (target.scrollTop === 0) {
                        this.onTop = true
                  }
                  else {
                        this.onBottom = false;
                        this.onTop = false;
                  }

                  this.flushDisplayItems();
            }
      },
      watch: {
            // 
            collection () {
                  // 
                  this.groupMessage.forEach(manager => manager.dispose());
                  // 
                  this.groupMessage = [];
                  this.totalHeight = 0;
                  this.totalWidth = 0;

                  this.onCollectionChanged();
            },
            onBottom (value) {
                  if (value) {
                        this.$emit('on-reach-bottom');
                  }
            },
            onTop (value) {
                  if (value) {
                        this.$emit('on-reach-top');
                  }
            }
      }
}
</script>



