<template>
      <!--  -->
      <span :class="classes" v-if="dot">
            <slot></slot>
            <transition :name="`${prefixCls}-fade`">
                  <span :style="styles" :class="dotClasses" v-show="showBadge"></span>
            </transition>
      </span>
      <!--  -->
      <span :class="classes" v-else>
            <slot></slot>
            <transition :name="`${prefixCls}-fade`">
                  <span :style="styles" :class="countClasses" v-show="showBadge">{{finalCount}}</span>
            </transition>
      </span>
</template>

<script>
const prefixCls = 'ivue-badge';

function isCssColor (color) {
      return !!color && !!color.match(/^(#|(rgb|hsl)a?\()/)
}

export default {
      name: prefixCls,
      props: {
            /*
            * 
            * 
            * @type{Number}
            */
            count: Number,
            /*
            * 
            * 
            * @type{Number, String}
            */
            overflowCount: {
                  type: [Number, String],
                  default: 99
            },
            /*
            * 
            *
            * type {Array}
            */
            offset: {
                  type: Array
            },
            /*
            * 
            *
            * @type {Boolean}
            */
            dot: {
                  type: Boolean,
                  default: false
            },
            /*
            * 0
            *
            * @type {Boolean}
            */
            showZero: {
                  type: Boolean,
                  default: false
            },
            /*
            * 
            *
            * @type {String}
            */
            text: {
                  type: String,
                  default: ''
            },
            /*
            * 
            *
            * @type {String}
            */
            color: {
                  type: String,
                  default: '#ed4014'
            },
            // 
            show: {
                  type: Boolean,
                  default: true
            }
      },
      data () {
            return {
                  prefixCls: prefixCls
            }
      },
      computed: {
            classes () {
                  return `${prefixCls}`;
            },
            // dot
            dotClasses () {
                  let _color = {};

                  if (isCssColor(this.color)) {
                        _color = { 'background-color': `${this.color}` };
                  }
                  else if (this.color) {
                        _color = { [this.color]: true }
                  }

                  return [
                        `${prefixCls}-dot`,
                        _color
                  ]
            },
            countClasses () {
                  let _color = {};

                  if (isCssColor(this.color)) {
                        _color = { 'background-color': `${this.color}` };
                  }
                  else if (this.color) {
                        _color = { [this.color]: true }
                  }
                  return [
                        `${prefixCls}-count`,
                        _color
                  ]
            },
            // 
            finalCount () {
                  const { count, overflowCount, text } = this;

                  // 
                  if (text !== '') {
                        return text;
                  }

                  //  >=   ? + : 
                  return parseInt(count) >= parseInt(overflowCount) ? `${overflowCount}+` : count;
            },
            // 
            showBadge () {
                  const { count, dot, showZero, text } = this;

                  let status = false;


                  if (!this.show) {
                        return this.show
                  }

                  // 
                  if (count) {
                        // 0
                        status = !(parseInt(count) === 0);
                  }

                  // 
                  if (dot) {
                        status = true;
                        if (count !== null) {
                              // 0
                              if (parseInt(count) === 0) {
                                    status = false;
                              }
                        }
                  }

                  // 
                  if (text !== '') {
                        status = true;
                  }

                  // 0 ||  0
                  return status || showZero;
            },
            // 
            styles () {
                  const style = {};

                  if (this.offset && this.offset.length === 2) {
                        style['margin-top'] = `${this.offset[0]}px`;
                        style['margin-right'] = `${this.offset[1]}px`;
                  }

                  if (isCssColor(this.color)) {
                        style['background-color'] = this.color;
                  }

                  return style;
            }
      }
}
</script>



