<script>
const returnArrayFn = () => [];

export default {
      props: {
            /*
            * 
            * 
            * @type {Array}
            */
            options: {
                  type: Array,
                  default: returnArrayFn
            },
            /*
            * 
            * 
            * @type {Array}
            */
            slotOption: {
                  type: Array,
                  default: returnArrayFn
            },
            /*
            * 
            * 
            * @type {Function}
            */
            slotUpdateHook: {
                  type: Function,
                  default: () => { }
            }
      },
      // 
      functional: true,
      render (h, { props, parent }) {

            // 
            if (props.slotOption !== parent.$slots.default) {
                  props.slotUpdateHook();
            }

            return props.options;
      }
}
</script>




